import { FC } from 'react'
import cn from 'classnames'
import plugImg from '../../../assets/img/plugVape.webp'
import {
  addItemsToCart,
  CartItemType,
  minusItem,
  removeItemsFromCart,
} from '../../../redux/slices/cartSlice'
import { useAppDispatch } from '../../../types/types'
import styles from './CartItem.module.scss'
import '../../../scss/_button.scss'

type PropsType = {
  id: string
  title: string
  imageUrl: string
  count: number
  price: number
  type: string
  size: number
}

export const CartItem: FC<PropsType> = ({
  id,
  title,
  imageUrl,
  count,
  size,
  type,
  price,
}) => {
  const dispatch = useAppDispatch()

  const onClickPlus = () => {
    dispatch(addItemsToCart({ id } as CartItemType))
  }

  const onClickMinus = () => {
    dispatch(minusItem(id))
  }

  const onClickRemove = () => {
    if (window.confirm('Do you want to empty the cart?')) {
      dispatch(removeItemsFromCart(id))
    }
  }

  return (
    <div className={styles.content__items}>
      <div className={styles.cart__item}>
        <div className={styles.cart__item__img}>
          <img src={imageUrl ? imageUrl : plugImg} alt='Pizza' />
        </div>
        <div className={styles.cart__item__info}>
          <h3>{title}</h3>
          <p>
            Nicotine {type}, {size} pfs.
          </p>
        </div>
        <div className={styles.cart__item__count}>
          <button
            onClick={onClickMinus}
            className={cn(
              styles.cart__item__count_minus,
              'button button--outline button--circle'
            )}
          >
            <svg
              width='10'
              height='10'
              viewBox='0 0 10 10'
              fill='none'
              xmlns='http://www.w3.org/2000/svg'
            >
              <path
                d='M5.92001 3.84V5.76V8.64C5.92001 9.17016 5.49017 9.6 4.96001 9.6C4.42985 9.6 4.00001 9.17016 4.00001 8.64L4 5.76L4.00001 3.84V0.96C4.00001 0.42984 4.42985 0 4.96001 0C5.49017 0 5.92001 0.42984 5.92001 0.96V3.84Z'
                fill='#EB5A1E'
              />
              <path
                d='M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z'
                fill='#EB5A1E'
              />
            </svg>
          </button>
          <b>{count}</b>
          <button
            onClick={onClickPlus}
            className={cn(
              styles.cart__item__count_plus,
              'button button--outline button--circle'
            )}
          >
            <svg
              width='10'
              height='10'
              viewBox='0 0 10 10'
              fill='none'
              xmlns='http://www.w3.org/2000/svg'
            >
              <path
                d='M5.92001 3.84V5.76V8.64C5.92001 9.17016 5.49017 9.6 4.96001 9.6C4.42985 9.6 4.00001 9.17016 4.00001 8.64L4 5.76L4.00001 3.84V0.96C4.00001 0.42984 4.42985 0 4.96001 0C5.49017 0 5.92001 0.42984 5.92001 0.96V3.84Z'
                fill='#EB5A1E'
              />
              <path
                d='M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z'
                fill='#EB5A1E'
              />
            </svg>
          </button>
        </div>
        <div className={styles.cart__item__price}>
          <b>{price * count} $</b>
        </div>
        <div onClick={onClickRemove} className={cn(styles.cart__item__remove)}>
          <button
            className={cn(
              styles.cart__item__remove_button,
              'button button--outline button--circle'
            )}
          >
            <svg
              width='10'
              height='10'
              viewBox='0 0 10 10'
              fill='none'
              xmlns='http://www.w3.org/2000/svg'
            >
              <path
                d='M5.92001 3.84V5.76V8.64C5.92001 9.17016 5.49017 9.6 4.96001 9.6C4.42985 9.6 4.00001 9.17016 4.00001 8.64L4 5.76L4.00001 3.84V0.96C4.00001 0.42984 4.42985 0 4.96001 0C5.49017 0 5.92001 0.42984 5.92001 0.96V3.84Z'
                fill='#EB5A1E'
              />
              <path
                d='M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z'
                fill='#EB5A1E'
              />
            </svg>
          </button>
        </div>
      </div>
    </div>
  )
}
